.Hero {
  position: relative;
  width: 600px;
  margin: 0 auto; }
  .Hero .TextWrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 300px;
    margin: 38.5% auto 0 auto; }
    .Hero .TextWrap .Title h3 {
      font-size: 34px;
      line-height: 37px;
      letter-spacing: -1px;
      font-weight: 400; }
    .Hero .TextWrap .Title h2 {
      position: relative;
      height: 52px;
      font-size: 46px;
      font-weight: 700;
      margin-top: 16px;
      font-style: italic; }
      .Hero .TextWrap .Title h2 span {
        display: inline-block;
        position: absolute;
        top: 0;
        opacity: 0; }
      .Hero .TextWrap .Title h2 span:nth-child(1) {
        -webkit-animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s infinite;
                animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s infinite; }
      .Hero .TextWrap .Title h2 span:nth-child(2) {
        -webkit-animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 3s infinite;
                animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 3s infinite; }
      .Hero .TextWrap .Title h2 span:nth-child(3) {
        -webkit-animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 6s infinite;
                animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 6s infinite; }
      .Hero .TextWrap .Title h2 span:nth-child(4) {
        -webkit-animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 9s infinite;
                animation: WordAnimation 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) 9s infinite; }
      .Hero .TextWrap .Title h2 span:before {
        content: '';
        position: absolute;
        top: 0;
        left: 22px;
        width: 45%;
        height: 100%;
        z-index: -1;
        background: #2fffcb;
        -webkit-transform: skew(-25deg);
                transform: skew(-25deg); }
    .Hero .TextWrap .Info {
      color: #ff5d00;
      font-size: 14px;
      font-weight: 700;
      margin-top: 23.2%; }
      .Hero .TextWrap .Info > a {
        display: inline-block;
        color: #ff5d00;
        margin-top: 4px; }
    .Hero .TextWrap > a {
      margin-top: 10.6%; }
  .Hero .Wrap {
    position: relative;
    padding-bottom: 118%; }
    .Hero .Wrap svg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%; }
  @media (max-width: 720px) {
    .Hero {
      width: auto;
      padding: 0 24px;
      margin-top: 32px; }
      .Hero .Wrap {
        padding-bottom: 0; }
        .Hero .Wrap svg {
          display: none; }
      .Hero .TextWrap {
        position: relative;
        width: 100%;
        margin: 0; }
        .Hero .TextWrap .Title h3 {
          font-size: 24px; }
        .Hero .TextWrap .Title h2 {
          font-size: 32px;
          height: 38px; }
        .Hero .TextWrap .Title span:before {
          left: 15px; }
        .Hero .TextWrap .Info {
          margin-top: 30px;
          font-size: 12px; }
        .Hero .TextWrap > a {
          margin-top: 32px; } }

@-webkit-keyframes WordAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px); }
  2% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0); }
  14% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0); }
  16% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0); }
  17% {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px); }
  80% {
    opacity: 0; }
  100% {
    opacity: 0; } }

@keyframes WordAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px); }
  2% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0); }
  14% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0); }
  16% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0); }
  17% {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px); }
  80% {
    opacity: 0; }
  100% {
    opacity: 0; } }
