.CoC {
  margin-top: 40px; }
  .CoC h3 {
    font-size: 21px;
    font-weight: 700;
    color: #ff5d00; }
  .CoC p {
    margin: 16px 0 32px 0;
    line-height: 24px; }
  @media (max-width: 720px) {
    .CoC {
      margin-top: 32px; }
      .CoC h3 {
        font-size: 13px; }
      .CoC p {
        margin-top: 12px;
        font-size: 14px;
        line-height: 20px; } }
