.Track {
  position: relative;
  width: 50%; }
  .Track .Title {
    font-size: 21px;
    font-weight: 700;
    color: #230462;
    text-align: center;
    margin-bottom: 16px; }
  @media (max-width: 720px) {
    .Track {
      width: 100%; }
      .Track .Title {
        display: none; }
      .Track:not(.selected) {
        display: none; } }
