@import url(~normalize.css/normalize.css);
html, body {
  font-family: "Roboto", sans-serif; }

h1, h2, h3, p {
  margin: 0; }

a {
  color: black;
  text-decoration: none; }

:global body.fixed {
  overflow: hidden; }

:global .wrap {
  padding-top: 131px; }

:global .container {
  max-width: 1024px;
  padding: 0 60px;
  margin: 0 auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

:global .extended {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

:global .kr-text {
  font-family: "Noto Sans KR", sans-serif; }

@media (min-width: 720px) and (max-width: 1024px) {
  :global .container {
    padding: 0 30px; } }

@media (max-width: 720px) {
  :global .wrap {
    padding-top: 60px; }
  :global .container {
    padding: 0 24px; } }
